<template>
  <div class="compaign-page">
    <div class="content">
      <div class="col-side">
        <QuickMenu />

        <FilterMain />
      </div>
      <div class="col-side-big">
        <div class="documents">
          <!-- <div class="title">Core Campaign 2024 - Document Access</div> -->

          <DocumentAccess
            :data="docs"
            :title="{ text: ['Core Campaign 2024 - Document Access'], fontSize: '28' }"
          />
        </div>

        <div class="controls-wrap">
          <div class="title">Granular Controls</div>

          <div class="controls-bnts">
            <div
              class="btn"
              :class="{ active: activeTab == 'Talent' }"
              @click="activeTab = 'Talent'"
            >
              Talent
            </div>
            <div
              class="btn"
              :class="{ active: activeTab == 'Theme' }"
              @click="activeTab = 'Theme'"
            >
              Theme
            </div>
            <div
              class="btn"
              :class="{ active: activeTab == 'Brand' }"
              @click="activeTab = 'Brand'"
            >
              Brand
            </div>
            <div
              class="btn"
              :class="{ active: activeTab == 'Location' }"
              @click="activeTab = 'Location'"
            >
              Location
            </div>
          </div>
          <div class="controls-items">
            <div v-for="(item, i) in activeItems" :key="i">
              <div class="item">
                <img :src="item.image" alt="" />
                <div class="title">{{ item.title }}</div>
                <div class="checkbox">
                  <div class="toggle-container">
                    <label class="switch">
                      <input type="checkbox" v-model="item.active" />
                      <span class="slider"></span>
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted, computed } from "vue";
import DocumentAccess from "@/components/widgets/DocumentAccess.vue";
import apiClient from "@/services/api";
import { defineProps } from "vue";
import QuickMenu from "@/components/QuickMenu.vue";

const props = defineProps({
  id: {
    type: String,
  },
});

// import { useStore } from "@/store";

// const store = useStore();

let activeTab = ref("Talent");
let items = ref([{}]);

let docs = ref([]);

onMounted(() => {
  apiClient.get(`/api/campaings/${props.id}`).then((data) => {
    items.value = data.data.items;

    console.log(data.data.items, "dataaa");
  });

  apiClient.get(`/api/dashboard/052452de-e8a2-46b5-a8ee-5ac593e05cb0`).then((data) => {
    data.data.widgets.forEach((item) => {
      if (item.type == "campaign-document-access") {
        docs.value = item.data;
      }
    });

    console.log(docs.value, "data1231aa");
  });
});

let activeItems = computed(() => {
  return items.value?.filter(
    (item) => Array.isArray(item.type) && item.type.includes(activeTab.value)
  );
});
</script>

